export type Paginated<T> = {
  results: T[];
  count: number;
  next: string | null;
  previous: string | null;
};

export function unwrapResults<T>(data: unknown): T[] {
  if (Array.isArray(data)) return data as T[];
  if (data && typeof data === "object" && "results" in data) {
    const r = (data as any).results;
    return Array.isArray(r) ? (r as T[]) : [];
  }
  return [];
}

export function isPaginated<T>(data: unknown): data is Paginated<T> {
  if (data === null || data === undefined) return false;
  if (typeof data !== "object") return false;
  if (!("results" in data)) return false;
  return Array.isArray((data as any).results);
}

export function getCount(data: unknown): number {
  if (isPaginated(data)) return data.count;
  if (Array.isArray(data)) return data.length;
  return 0;
}
